.container {
  max-width: 1112px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

.section {
  & {
    padding: 40px 0;
  }
}

.section-title {
  & {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__lg {
    font-weight: 700;
    color: var(--text-color-dark);
    text-transform: uppercase;
  }
  &__sm {
    font-size: 15px;
    margin-top: 5px;
    font-weight: 600;
    color: var(--text-color-primary);
    letter-spacing: .3px;
  }
}

