:root {
  --text-size-base: 16px;

  --text-size-h1: 50px;
  --text-size-h2: 40px;
  --text-size-h3: 33px;
  --text-size-h4: 22px;

  --color-primary: #705EDC;

  --text-color-primary: rgb(116, 112, 112);
  --text-color-dark: #0e2431;
  --text-color-light: rgb(255, 255, 255);

  --header-height: 70px;

  --z-index-header: 999;
  --z-index-links-panel: 998;
  --z-index-up-btn: 997;
  --z-index-bottom-menu: 996;
  --z-index-skills-popup: 995;
  --z-index-skills-category: 994;
}

$bp-desktop: 1200px;
$bp-tablet: 768px;
$bp-mobile: 480px;

@media all and (max-width: $bp-mobile) {
  :root {
    --text-size-h1: 40px;
    --text-size-h2: 33px;
  }
}
