.bottom-menu {
  & {
    display: none;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
    color: var(--text-color-dark);
    z-index: var(--z-index-bottom-menu);
    background-color: rgb(255, 255, 255);
  }
  &__body {
    height: 40px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo {
    font-weight: 900;
  }
  &__right {
    display: flex;

    &--hidden {
      display: none;
    }
  }
  &__btn {
    @include gutter-h(5px);
  }
  &__btn:not(:nth-child(1)) {
    margin-left: 10px;
  }
  &__icon {
    font-size: 22px;
  }
  &__links-panel {
    padding: 20px 0 50px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    background-color: white;
    box-shadow: 0 0 10px rgba(0, 0, 0, .2);
    bottom: 0;
    left: 0;
    right: 0;
    transform: translateY(100%);
    transition: .2s transform linear;
    z-index: var(--z-index-links-panel);

    &--opened {
      transform: translateY(0%);
    }
  }
  &__close-links-btn {
    position: absolute;
    bottom: 10px;
    right: 15px;
    font-size: 30px;
    color: var(--text-color-dark);
  }
  &__links {
    display: grid;
    grid-template-columns: auto auto auto;
    grid-template-rows: auto auto;
    grid-row-gap: 30px;
    grid-column-gap: 50px;
  }
  &__link {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: var(--text-color-primary);

    &--active {
      color: var(--color-primary);
    }
  }
  &__link-icon {
    font-size: 23px;
  }
  &__link-text {
    margin-top: 5px;
    font-size: 15px;
    letter-spacing: .5px;
  }
}

@media all and (max-width: $bp-tablet) {
  .bottom-menu {
    & {
      display: block;
    }
  }
}
