.footer {
  & {
    background-color: var(--color-primary);
    color: var(--text-color-light);
  }
  &__container {
    
  }
  &__body {
    padding: 40px 0 150px;
    display: flex;
    position: relative;
    
    & > * {
      flex-basis: 33%;
      flex-grow: 1;
    }
  }
  &__left {
    display: flex;
    flex-direction: column;

  }
  &__center {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__right {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__logo-lg {
    font-size: 50px;
    font-weight: 600;
  }
  &__logo-sm {
    font-size: 20px;
    margin-top: 10px;
  }
  &__links {
    display: flex;
  }
  &__link {
    font-size: 18px;
    @include gutter-h(20px);
    cursor: pointer;

    &:hover {
      text-decoration: underline;
    }
  }
  &__social-links {
    display: flex;
  }
  &__social-link {
    cursor: pointer;
    @include gutter-h(20px);
  }
  &__social-icon {
    color: rgb(255, 255, 255);
    font-size: 25px;
  }
  &__rights {
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    color: rgba(0, 0, 0, 0.57);
  }
}

@media all and (max-width: $bp-mobile) {
  .footer {
    & {

    }
    &__body {
      flex-direction: column;

      & > * {
        @include gutter-v(40px);
      }
    }
    &__center {
      
      justify-content: flex-start;
    }
    &__right {
      justify-content: flex-start;
    }
    &__links {
      flex-direction: column;
    }
    &__link {
      @include gutter-v(20px);
      @include gutter-h(0);
    }
    &__rights {
      bottom: 70px;
    }
  }
}
