.project {
  & {
    display: flex;
    justify-content: space-between;
    padding: 50px 0 50px 0;
    // @include b;
  }
  &__left {
    // @include b(1);
    flex-basis: 47%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  &__right {
    // @include b(1);
    flex-basis: 49%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  &__right > *:not(:nth-child(1)) {
    margin-top: 20px;
  }
  &__img {
    width: 350px;
    height: 250px;
    border-radius: 10px;
    object-fit: cover;
  }
  &__title {
    color: var(--text-color-dark);
    font-size: 30px;
    font-weight: 600;
  }
  &__desc {
    max-width: 300px;
  }
  &__btns {
    display: flex;
  }
  &__btns > *:not(:nth-child(1)) {
    margin-left: 10px;
  }
  &__btn .button__icon {
    position: relative !important;
    transition: left 2s linear;
  }
  &__btn:hover .button__icon {
    left: 10px;
  }
}

@media all and (max-width: $bp-tablet) {
  .project {
    & {
      flex-direction: column;
      align-self: stretch;
      padding: 0 50px 50px 50px;
    }
    &__left, &__right {
      flex-basis: none;
      // @include b;
    }
    &__left {
      justify-content: center;
    }
    &__right {
      margin-top: 20px;
    }
  }
}

@media all and (max-width: $bp-mobile) {
  .project {
    &__img {
      width: 100%;
      height: 200px;
    }
  }
}
