.header {
  & {
    height: var(--header-height);
    display: flex;
    color: var(--text-color-dark);
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    transition: box-shadow .2s ease-in;
    background-color: white;
    z-index: var(--z-index-header);
  }
  &--with-shadow {
    box-shadow: 0 0 3px 1px rgba(0, 0, 0, 0.2);
  }
  &__container {
    display: flex;
    align-items: center;
  }
  &__body {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo {
    font-size: 20px;
    font-weight: 900;
  }
  &__nav-list {
    display: flex;
  }
  &__nav-link {
    & {
      @include gutter-h(40px);
      font-weight: 500;
      font-size: 18px;
      cursor: pointer;
    }
    &--active {
      color: var(--color-primary);
    }
  }
}

@media all and (max-width: $bp-tablet) {
  .header {
    display: none;
  }
}
