.about {
  & {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
  }
  &__body {
    margin-top: 40px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    & > * {
      // @include b;
      width: 40%;    }
  }
  &__img-container {
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__img {
    max-width: 100%;
    border-radius: 15px;
  }
  &__main {
    margin-left: 30px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    & > * {
      @include gutter-v(30px);
    }
  }
  &__text {
    text-align: start;
  }
  &__info-items {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  &__info-item {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__info-item-num {
    font-size: 30px;
    font-weight: 500;
    color: var(--text-color-dark);
  }
  &__info-item-desc {
    margin-top: 10px;
    color: var(--text-color-primary);
    white-space: pre-line;
    text-align: center;
    line-height: 20px;
    font-weight: 500;
  }
}

@media all and (max-width: $bp-tablet) {
  .about {
    & {
    }
    &__body {
      flex-direction: column;

      & > * {
        margin-left: 0;
        width: 80%;
      }
    }
    &__main {
      margin-top: 20px;
    }
  }
}

@media all and (max-width: $bp-mobile) {
  .about {
    & {
    }
    &__body {
      & > * {
        width: 100%;
      }
    }
  }
}
