.home {
  & {
    // @include b;
  }
  &__body {
    // @include b;
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 100vh;

    & > * {
      // @include b(1, blue);
    }
  }
  &__social-section {

  }
  &__main-section {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    & > * {
      @include gutter-v(15px);
    }
  }
  &__avatar-section {

    // home__avatar-section .social-links
    .home__social-links {
      display: none;
    }
  }
  &__social-links {
    display: flex;
    flex-direction: column;
  }
  &__social-link {
    @include gutter-v(20px);
  }
  &__social-icon {
    font-size: 25px !important;
    color: var(--text-color-dark);
    cursor: pointer;

    &:hover {
      color: var(--color-primary);
    }
  }
  &__title-sm {
    font-size: 25px;
    font-weight: 600;
  }
  &__about {
    max-width: 400px;
  }
  &__avatar {
    max-width: 350px;
    border-radius: 50%;
  }
  &__scroll-down {
    margin-top: 50px !important;
    display: flex;
    align-items: center;
    color: var(--text-color-dark);
    font-size: 18px;
    font-weight: 400;
    position: relative;
    animation-name: up-down;
    animation-duration: 1.5s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-direction: alternate-reverse;

    & > * {
      @include gutter-h(5px);
    }
  }
  &__mouse-icon {
    color: var(--color-primary);
    font-size: 35px;
  }
  &__arrow-down-icon {
    color: var(--color-primary);
    font-size: 30px;
  }
}

@keyframes up-down {
  from {
    top: 0px;
  }

  to {
    top: 20px;
  }
}

@media all and (max-width: $bp-tablet) {
  .home {
    & {

    }
    &__body {
      flex-direction: column-reverse;

      & > * {
        width: 100%;
      }
    }
    &__social-section {
      display: none;
    }
    &__scroll-down {
      display: none;
    }
    &__main-section {
      margin-top: 20px;
    }
    &__avatar-section {
      text-align: center;
      position: relative;

      .home__social-links {
        display: flex;
        flex-direction: column;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
      }
    }
    &__avatar {
      width: 50%;
    }
  }
}
